
import { defineComponent, watchEffect } from 'vue';
import Stats from '@/components/Stats.vue';
import Cipher from '@/components/Cipher.vue';
import cipher, { Mode } from '@/modules/caesar-cipher';
import { get } from '@/api';

export default defineComponent({
  name: 'Home',
  components: {
    Stats,
    Cipher,
  },
  data() {
    return {
      text: '',
      dictionary:
        'АБВГДЕЁЖЗИЙКЛМНОПРСТУФХЦЧШЩЪЫЬЭЮЯABCDEFGHIJKLMNOPQRSTUVWXYZ1234567890!@#$%^&*;:()_+-., ',
      offset: 2,
      inverse: false,
    };
  },
  computed: {
    encoded(): string {
      return cipher({
        string: this.text,
        offset: this.offset,
        dictionary: this.dictionary,
        mode: !this.inverse ? Mode.Encode : Mode.Decode,
      });
    },
    decoded(): string {
      return cipher({
        string: this.encoded,
        offset: this.offset,
        dictionary: this.dictionary,
        mode: !this.inverse ? Mode.Decode : Mode.Encode,
      });
    },
  },
  mounted() {
    watchEffect(
      (onInvalidate) => {
        const [response, controller] = get('text.txt');
        response.then((text) => {
          this.text = text;
        });

        onInvalidate(() => controller.abort());
      },
      { flush: 'post' }
    );
  },
});
