
import { defineComponent, ref, toRefs } from 'vue';

export default defineComponent({
  name: 'FrequencyChart',
  props: {
    title: {
      type: String,
      required: true,
    },
    labels: {
      type: Array as () => string[],
      required: true,
    },
    current: {
      type: Array as () => { x: string; y: number }[],
      default: () => [],
    },
    previous: {
      type: Array as () => { x: string; y: number }[],
      default: () => [],
    },
  },

  setup(props) {
    const data = toRefs(props);

    const chartOptions = ref({
      dataLabels: {
        enabled: false,
      },
      xaxis: {
        categories: data.labels,
      },
    });

    const series = ref([
      {
        name: 'Предыдущее',
        data: data.previous,
      },
      {
        name: 'Текущее',
        data: data.current,
      },
    ]);

    return { chartOptions, series };
  },
});
